<template>
  <div class="map-box">
    <baidu-map
      class="bm-view"
      :center="center"
      :zoom="zoom"
      @ready="mapReload"
      ak="lh1aVcYlOPVBVffwo9cSGZHmoyiCeKzz"
    >
      <bm-marker :position="center" :dragging="true" @click="infoWindowOpen"></bm-marker>
      <bm-info-window
        :position="center"
        :width=260
        title='<div style="font-size:14px;color:#333;padding-bottom:12px"></div>'
        :show="infoWindow.show"
        @close="infoWindowClose"
        @open="infoWindowOpen"
      >
        <p v-text="infoWindow.contents"></p>
      </bm-info-window>
    </baidu-map>
  </div>
</template>

<script>
import BaiduMap from "vue-baidu-map/components/map/Map";
import BmMarker from "vue-baidu-map/components/overlays/Marker";
// import BmLabel from "vue-baidu-map/components/overlays/Label";
import BmInfoWindow from "vue-baidu-map/components/overlays/InfoWindow";

export default {
  name: "Map",
  props:{
      
  },
  data() {
    return {
      center: { lng: 110.206599, lat: 36.872989 },
      zoom: 15,
      infoWindow: {
        show: true,
        contents:``
      }
    };
  },
  components: {
    BaiduMap,
    BmMarker,
    // BmLabel,
    BmInfoWindow
  },
  methods: {
      infoTitle(){
          return(
              <div style="font-size:14px;color:#333"></div>
          )
      },
    mapReload() {
      this.center.lng = 110.206599;
      this.center.lat = 36.872989;
      this.zoom = 15;
    },
    infoWindowClose() {
      this.infoWindow.show = false;
    },
    infoWindowOpen() {
      this.infoWindow.show = true;
    }
  }
};
</script>

<style lang="less" scoped>
.map-box {
  padding: 10px;
